 * Basic styling for comment module.
 */
/**
 * Indent threaded comments.
 */
.indented {
  margin-left: 1.5rem;
  /* LTR */
}

[dir=rtl] .indented {
  margin-left: 0;
  margin-right: 1.5rem;
}

/**
 * @file
 * Visual styles for Bootstrap's item list.
 */
.item-list ul {
  list-style: none;
  margin: 0 0 0.5rem 0;
  padding: 0;
  /* LTR */
}

[dir=rtl] .item-list ul {
  padding: 0;
}

.item-list ul li {
  /* This is needed to reset styles in item-list.theme.css */
  margin: 0;
  padding: 0.5rem 1rem 0 0;
  /* LTR */
}

[dir=rtl] .item-list ul li {
  /* This is needed to reset [dir="rtl"] styles in item-list.theme.css */
  margin: 0;
  padding: 0.5rem 0 0 1rem;
}

.item-list .item-list__comma-list,
.item-list .item-list__comma-list li,
[dir=rtl] .item-list .item-list__comma-list,
[dir=rtl] .item-list .item-list__comma-list li {
  padding: 0;
}

/**
 * @file
 * List group.
 * A list of links, grouped together.
 */
.list-group__link {
  border-top: 1px solid #dee2e6;
  padding: 0.5rem 0 0;
}

.list-group__description {
  margin: 0 0 0.5rem;
}

/**
 * @file
 * Media related  specific CSS.
 */
/* Media Image */
.field--name-field-media-image img {
  max-width: 100%;
  height: auto;
}

/* Node Dialog Add/Select Overlay */
/* Two Column Layout */
.media-library-wrapper {
  display: flex;
  flex-wrap: wrap;
}

/* Left Menu Layout */
.media-library-menu {
  margin: 5px 5px 5px 0;
  padding: 20px 15px;
  border: 1px solid #dee2e6;
  border-radius: 3px;
  background: #e9ecef;
  height: 100%;
}

.media-library-menu li {
  margin: 5px;
  white-space: nowrap;
  height: 45px;
}

.media-library-menu li::marker {
  color: #e9ecef;
}

.media-library-menu__link {
  margin: 5px 5px 5px 0;
  padding: 10px 15px;
  border: 1px solid #dee2e6;
  border-radius: 3px;
  background: #e9ecef;
}

.active.media-library-menu__link {
  background: #fff;
}

/* Form Elements Layout  */
.media-library-content {
  flex: 2;
}

.media-library-wrapper .form--inline {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
}

.media-library-widget-modal .js-form-item {
  margin: 5px;
}

.media-library-wrapper .form-type-textfield {
  flex: 2;
}

/* Fix Form Insert Button to Bottom of Popup */
.media-library-widget-modal .form-actions {
  position: sticky;
  bottom: 0;
  background: #fff;
  padding: 5px;
}

.media-library-widget-modal .ui-dialog-content {
  padding-bottom: 0;
}

/* Grid Layout */
.ui-dialog.ui-corner-all.ui-widget.ui-widget-content.ui-front.media-library-widget-modal.ui-dialog--narrow {
  width: 80% !important;
  left: 10% !important;
}

.media-library-views-form__rows {
  display: flex;
  flex-wrap: wrap;
}

.media-library-item--grid {
  margin: 10px;
  width: 220px;
}

.media-library-item--grid .js-form-type-checkbox, .media-library-item--grid .form-check-label {
  padding-left: 0;
}

/* Add or Select Media */
.media-library-add-form__media::marker {
  color: #fff;
}

/**
 * @file
 * Visual styles for Bootstrap's node component.
 */
.node__content {
  font-size: 1rem;
  margin-top: 1rem;
}

/* View mode teaser styles. */
.node--view-mode-teaser {
  border-bottom: 1px solid #dee2e6;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
}

.node--view-mode-teaser h2, .node--view-mode-teaser .hero-headline-wrapper .hero-subhead, .hero-headline-wrapper .node--view-mode-teaser .hero-subhead, .node--view-mode-teaser .h2 {
  margin-top: 0;
  padding-top: 0.5em;
}

.node--view-mode-teaser.node--sticky {
  background: #f9f9f9;
  background: rgba(0, 0, 0, 0.024);
  border: 1px solid #dee2e6;
  padding: 0 1rem 1rem;
}

.node--view-mode-teaser .node__content {
  clear: none;
  font-size: 1rem;
  line-height: 1.5;
}

/* Node metadata styles. */
.node__meta {
  font-size: 0.875rem;
  margin-bottom: -5px;
}

.node__meta .field--name-field-user-picture img {
  float: left;
  /* LTR */
  margin: 1px 1rem 0 0;
  /* LTR */
}

[dir=rtl] .node__meta .field--name-field-user-picture img {
  float: right;
  margin-left: 1rem;
  margin-right: 0;
}

/* Node links styles. */
.node__links {
  text-align: right;
  /* LTR */
  font-size: 1rem;
}

[dir=rtl] .node__links {
  text-align: left;
}

/* Unpublished node styles. */
.node--unpublished {
  padding: 1rem 1rem 0;
}

.node--unpublished .comment-text .comment-arrow {
  border-left: 1px solid #fff4f4;
  border-right: 1px solid #fff4f4;
}

.node-preview-container {
  background: #d1e8f5;
  background-image: -webkit-linear-gradient(top, #d1e8f5, #d3e8f4);
  background-image: linear-gradient(to bottom, #d1e8f5, #d3e8f4);
  font-family: Arial, sans-serif;
  box-shadow: 0 1px 3px 1px rgba(0, 0, 0, 0.3333);
  position: fixed;
  z-index: 499;
  width: 100%;
  padding: 10px;
}

.node-preview-backlink {
  background-color: #419ff1;
  background: url(/core/misc/icons/000000/chevron-left.svg) left no-repeat, -webkit-linear-gradient(top, #419ff1, #1076d5);
  background: url(/core/misc/icons/000000/chevron-left.svg) left no-repeat, linear-gradient(to bottom, #419ff1, #1076d5); /* LTR */
  border: 1px solid #0048c8;
  border-radius: 0.4em;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.4);
  color: #fff;
  font-size: 0.9em;
  line-height: normal;
  margin: 0;
  padding: 4px 1em 4px 0.6em; /* LTR */
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.5);
}

[dir=rtl] .node-preview-backlink {
  background: url(/core/misc/icons/000000/chevron-right.svg) right no-repeat, -webkit-linear-gradient(top, #419ff1, #1076d5);
  background: url(/core/misc/icons/000000/chevron-right.svg) right no-repeat, linear-gradient(to bottom, #419ff1, #1076d5);
  padding: 4px 0.6em 4px 1em;
  float: right;
}

.node-preview-backlink:focus,
.node-preview-backlink:hover {
  background-color: #419cf1;
  background: url(/core/misc/icons/000000/chevron-left.svg) left no-repeat, -webkit-linear-gradient(top, #59abf3, #2a90ef);
  background: url(/core/misc/icons/000000/chevron-left.svg) left no-repeat, linear-gradient(to bottom, #59abf3, #2a90ef); /* LTR */
  border: 1px solid #0048c8;
  text-decoration: none;
  color: #fff;
}

[dir=rtl] .node-preview-backlink:focus,
[dir=rtl] .node-preview-backlink:hover {
  background: url(/core/misc/icons/000000/chevron-right.svg) right no-repeat, -webkit-linear-gradient(top, #59abf3, #2a90ef);
  background: url(/core/misc/icons/000000/chevron-right.svg) right no-repeat, linear-gradient(to bottom, #59abf3, #2a90ef);
}

.node-preview-backlink:active {
  background-color: #0e69be;
  background: url(/core/misc/icons/000000/chevron-left.svg) left no-repeat, -webkit-linear-gradient(top, #0e69be, #2a93ef);
  background: url(/core/misc/icons/000000/chevron-left.svg) left no-repeat, linear-gradient(to bottom, #0e69be, #2a93ef); /* LTR */
  border: 1px solid #0048c8;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.25);
}

[dir=rtl] .node-preview-backlink:active {
  background: url(/core/misc/icons/000000/chevron-right.svg) right no-repeat, -webkit-linear-gradient(top, #0e69be, #2a93ef);
  background: url(/core/misc/icons/000000/chevron-right.svg) right no-repeat, linear-gradient(to bottom, #0e69be, #2a93ef);
}

.node-preview-backlink::before {
  content: "";
  width: 10px;
  display: inline-block;
}

/**
 * @file
 * Styles for Bootstrap page.
 */
/* Hack for .placeholder issue https://www.drupal.org/project/drupal/issues/3233533 */
em.placeholder {
  vertical-align: unset !important;
  cursor: unset !important;
  background-color: unset !important;
  opacity: unset !important;
}

section.full-width {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

/**
 * @file
 * The visual styles for Bootstrap's search form(s).
 */
input[type=search] {
  box-sizing: border-box;
}

header #search-block-form {
  padding: 5px 15px;
  flex-flow: row wrap;
}

#search-block-form {
  display: flex;
}

/**
 * @file
 * Visual styles for the Shortcut action link in Bootstrap.
 */
.shortcut-wrapper {
  margin: 2.2em 0 1.1em 0; /* Same as usual .page-title margin. */
}

.shortcut-wrapper .page-title {
  float: left; /* LTR */
  margin: 0;
}

[dir=rtl] .shortcut-wrapper .page-title {
  float: right;
}

.shortcut-action {
  border-bottom: none;
  margin-left: 0.5em; /* LTR */
  padding-top: 0.35em;
}

[dir=rtl] .shortcut-action {
  margin-left: 0;
  margin-right: 0.5em;
}

.shortcut-action:hover,
.shortcut-action:active,
.shortcut-action:focus {
  border-bottom: none;
}

/* ------------------ Sidebar ----------------- */
.sidebar .block {
  border-style: solid;
  border-width: 1px;
  padding: 1rem 1rem;
  margin: 0 0 1rem;
}
.sidebar .block .content {
  font-size: 1rem;
  line-height: 1.25;
}
.sidebar h2, .sidebar .hero-headline-wrapper .hero-subhead, .hero-headline-wrapper .sidebar .hero-subhead, .sidebar .h2 {
  margin: 0 0 0.5rem;
  border-bottom: 1px solid #dee2e6;
  padding-bottom: 5px;
  text-shadow: 0 1px 0 #fff;
  font-size: 1.25rem;
  line-height: 1.2;
}
.sidebar tbody {
  border: none;
}
.sidebar tr.even,
.sidebar tr.odd {
  background: none;
  border-bottom: 1px solid #dee2e6;
}
.sidebar nav ul.nav {
  margin: 0 -1rem;
}

/**
 * @file
 * Footer Styling.
 */
.site-footer {
  padding: 1rem 0;
}
.site-footer .layout-container {
  padding: 0 1rem;
}

/* Footer Top Styling. */
.site-footer__top .content {
  margin-top: 0;
}

/* Footer Bottom Styling. */
.site-footer__bottom .block {
  margin: 0.5rem 0;
}

.site-footer__bottom .content {
  padding: 0.5rem 0;
  margin-top: 0;
}

/**
 * @file
 * Styles for the skip link.
 */
.skip-link {
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  z-index: 50;
  background: #495057;
  font-size: 1rem;
  line-height: 2;
  padding: 1px 1rem 2px;
  border-radius: 0 0 0.5rem 0.5rem;
  border-bottom-width: 0;
  outline: 0;
}

.skip-link.visually-hidden.focusable:focus {
  position: absolute !important;
  color: #fff;
}

/**
 * @file
 * Styles for Bootstrap's tables.
 */
.sticky-header thead th {
  vertical-align: bottom;
  border-bottom: calc(2 * var(--bs-border-width)) solid var(--bs-border-color);
}

.sticky-header th {
  padding: 0.5rem 0.5rem;
  vertical-align: top;
  border-top: var(--bs-border-width) solid var(--bs-border-color);
}

/**
 * @file
 * Visual styles for table drag.
 */
tr.drag {
  background-color: #fffff0;
}

tr.drag-previous {
  background-color: #ffd;
}

.tabledrag-changed.warning {
  margin-bottom: 0.5rem;
  color: var(--bs-warning);
}

a.tabledrag-handle .handle {
  width: 25px;
  height: 25px;
}

.tabledrag-cell-content > * {
  display: table-cell;
  vertical-align: middle;
}

/**
 * @file
 * Table select behavior.
 *
 * @see tableselect.js
 */
tr.selected td {
  background: #ffc;
}

td.checkbox,
th.checkbox {
  text-align: center;
}

[dir=rtl] td.checkbox,
[dir=rtl] th.checkbox {
  /* This is required to win over specificity of [dir="rtl"] td */
  text-align: center;
}

/**
 * @file
 * Table sort indicator.
 */
th.is-active img {
  display: inline;
}

td.is-active {
  background-color: #dee2e6;
}

/**
 * @file
 * Tablesort indicator styles.
 */
.tablesort {
  width: 14px;
  height: 14px;
  vertical-align: top;
  margin: 1px 0 0 5px;
}

.tablesort--asc {
  background-image: url(/core/misc/icons/ffffff/twistie-down.svg);
}

.tablesort--desc {
  background-image: url(/core/misc/icons/ffffff/twistie-up.svg);
}

/**
 * @file
 * Theme styling for tabs.
 */
nav.tabs {
  margin-bottom: 1rem;
}

nav.tabs ul {
  background: #e9ecef;
  display: flex;
}
nav.tabs ul .nav-item {
  flex-grow: 1;
}
nav.tabs ul .nav-item a {
  color: #343a40;
  text-align: center;
}
nav.tabs ul .nav-item.active a {
  background: rgb(98.13, 139.08, 165.54);
}

/**
 * @file
 * Visual styles for a resizable textarea.
 */
.form-textarea-wrapper textarea {
  display: block;
  margin: 0;
  width: 100%;
  box-sizing: border-box;
}

/**
 * @file
 * Visual styles for Bartik's ui-widget component.
 */
/**
 * @file
 * Styles for Bartik's modal windows
 */
.ui-widget-overlay {
  background: #000;
  opacity: 0.7;
}

.ui-dialog {
  border-radius: 0;
}

.ui-dialog input,
.ui-dialog select,
.ui-dialog .du-tuition-calculator-calculator .chosen-container-single.chosen-select .chosen-single,
.du-tuition-calculator-calculator .chosen-container-single.chosen-select .ui-dialog .chosen-single,
.ui-dialog textarea {
