footer {
  text-align: center;
}
footer .menu {
  flex-wrap: wrap;
}
footer .menu li {
  margin-left: 0;
}
footer .menu li a {
  color: white;
  font-size: 1.4rem;
  font-weight: normal;
  font-family: "BreveSans", sans-serif;
  text-decoration: none;
  padding: 1.3rem 1.8rem;
}
footer .menu li a:hover, footer .menu li a:focus {
  color: #ebd5ab;
  text-decoration: underline;
}
footer .bg-dark-gray {
  padding: 3.6rem 0;
}
footer .copyright {
  background-color: #000000;
  color: #797979;
  font-size: 1.4rem;
  padding: 1.5rem;
  font-family: "BreveSans", sans-serif;
  margin-bottom: 0;
}
@media (max-width: 767.98px) {
  footer .has-gray-band > .row, footer .paragraph--type--du-in-the-news-list .has-gray-band > .views-element-container, .paragraph--type--du-in-the-news-list footer .has-gray-band > .views-element-container, footer .has-gray-band > .paragraph--type--du-in-the-news-list, footer .show-pager.paragraph--type--du-in-the-news-list .has-gray-band > header, .show-pager.paragraph--type--du-in-the-news-list footer .has-gray-band > header {
    padding-left: 5.5rem;
    padding-right: 5.5rem;
  }
}
@media (max-width: 767.98px) and (max-width: 575.98px) {
  footer .has-gray-band > .row, footer .paragraph--type--du-in-the-news-list .has-gray-band > .views-element-container, .paragraph--type--du-in-the-news-list footer .has-gray-band > .views-element-container, footer .has-gray-band > .paragraph--type--du-in-the-news-list, footer .show-pager.paragraph--type--du-in-the-news-list .has-gray-band > header, .show-pager.paragraph--type--du-in-the-news-list footer .has-gray-band > header {
    padding-left: 1.8rem;
    padding-right: 1.8rem;
  }
}

.has-gray-band {
  position: relative;
  padding-bottom: 5rem;
}
.has-gray-band:after {
  content: "";
  background-color: #f4f4f4;
  height: 13.8rem;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
@media (max-width: 575.98px) {
  .has-gray-band {
    padding-bottom: 3rem;
  }
  .has-gray-band:after {
    height: 27.4rem;
  }
}

form.buffer-on-inner-row {
  margin-bottom: 5rem;
}
form label {
